/* ============================
   TERMS PAGE CONTAINER
============================ */
.terms-container {
  max-width: 900px;
  margin: 40px auto;
  padding: 20px;
  font-family: "Inter", sans-serif;
  color: #222;
  line-height: 1.7;
}

/* ============================
   TITLES & HEADINGS
============================ */
.terms-title {
  font-size: 32px;
  font-weight: 800;
  margin-bottom: 10px;
  color: #0d1a33;
}

.terms-meta {
  font-size: 14px;
  color: #666;
  margin-bottom: 5px;
}

.terms-content h2 {
  font-size: 22px;
  margin-top: 25px;
  margin-bottom: 10px;
  color: #0d1a33;
}

.terms-content h3 {
  font-size: 18px;
  margin-top: 18px;
  margin-bottom: 6px;
  color: #222;
}

/* ============================
   TEXT & PARAGRAPHS
============================ */
.terms-content p {
  margin-bottom: 12px;
  font-size: 16px;
  color: #333;
}

/* ============================
   LISTS
============================ */
.terms-content ul {
  margin-left: 20px;
  margin-bottom: 12px;
}

.terms-content ul li {
  margin-bottom: 6px;
  font-size: 16px;
}

/* ============================
   LINKS
============================ */
.terms-content a {
  color: #0654ba;
  text-decoration: none;
  font-weight: 500;
}

.terms-content a:hover {
  text-decoration: underline;
}

/* ============================
   RESPONSIVE
============================ */
@media (max-width: 768px) {
  .terms-title {
    font-size: 26px;
  }

  .terms-container {
    padding: 16px;
  }
}
