.guidelines-container {
  max-width: 900px;
  margin: 40px auto;
  padding: 20px;
  font-family: "Inter", sans-serif;
  color: #222;
  line-height: 1.7;
}

.guidelines-title {
  font-size: 32px;
  font-weight: 800;
  color: #0d1a33;
  margin-bottom: 10px;
}

.guidelines-meta {
  font-size: 14px;
  color: #666;
  margin-bottom: 5px;
}

.guidelines-content h2 {
  font-size: 22px;
  color: #0d1a33;
  margin-top: 25px;
  margin-bottom: 10px;
}

.guidelines-content h3 {
  font-size: 18px;
  margin-bottom: 8px;
}

.guidelines-content p {
  font-size: 16px;
  margin-bottom: 12px;
}

.guidelines-content ul {
  margin-left: 20px;
  margin-bottom: 14px;
}

.guidelines-content ul li {
  margin-bottom: 6px;
  font-size: 16px;
}

.guidelines-content a {
  color: #0654ba;
  text-decoration: none;
  font-weight: 500;
}

.guidelines-content a:hover {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .guidelines-title {
    font-size: 26px;
  }
  .guidelines-container {
    padding: 16px;
  }
}
